import { NgModule } from '@angular/core';
import { MatButtonModule } from '@angular/material/button';

import { BrowserModule } from '@angular/platform-browser';
import { RouterModule } from '@angular/router';
import { ScullyLibModule } from '@scullyio/ng-lib';
import { UiModule } from '@static-pages/shared/ui';
import { UtilRouterHelperModule } from '@static-pages/shared/util-router-helper';
import { CookieLawModule } from 'angular2-cookie-law';
import { AppComponent } from './app.component';

@NgModule({
  declarations: [AppComponent],
  imports: [
    BrowserModule,
    UiModule,
    UtilRouterHelperModule,
    RouterModule.forRoot(
      [
        {
          path: 'kontakt',
          loadChildren: () =>
            import('@static-pages/scheibentoenung-schmieder/contact').then(
              (m) => m.ContactModule
            ),
        },
        {
          path: 'folien',
          loadChildren: () =>
            import('@static-pages/scheibentoenung-schmieder/folien').then(
              (m) => m.FolienModule
            ),
        },
        {
          path: '',
          pathMatch: 'full',
          loadChildren: () =>
            import('@static-pages/scheibentoenung-schmieder/home').then(
              (m) => m.HomeModule
            ),
        },

        {
          path: '',
          loadChildren: () =>
            import('@static-pages/shared/static-pages').then(
              (m) => m.StaticPagesModule
            ),
        },
      ],
      {
        anchorScrolling: 'enabled',
        scrollPositionRestoration: 'enabled',
      }
    ),
    CookieLawModule,
    MatButtonModule,
  ],
  providers: [],
  bootstrap: [AppComponent],
})
export class AppModule {}
