import { Component, OnInit } from '@angular/core';
import { Meta, Title } from '@angular/platform-browser';
import { Router } from '@angular/router';
import { RouterHelperService } from '@static-pages/shared/util-router-helper';

@Component({
  selector: 'scheibentoenung-schmieder-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss'],
})
export class AppComponent implements OnInit {
  meta$ = this.routerHelperService.meta$;
  data$ = this.routerHelperService.data$;

  constructor(
    private router: Router,

    public routerHelperService: RouterHelperService,
    private titleService: Title,
    private metaService: Meta
  ) {}

  ngOnInit() {
    this.meta$.subscribe(({ title, description, cover }) => {
      this.titleService.setTitle(title);
      this.metaService.updateTag({ name: 'description', content: description });
      this.metaService.updateTag({
        property: 'og:description',
        content: description,
      });
      this.metaService.updateTag({
        property: 'og:image',
        content: '/assets/img/cover/' + cover,
      });

      this.metaService.updateTag({ property: 'og:title', content: title });
    });
  }
}
